import React from "react"
import HoverLink from "./hoverlink.js"
import { Link } from "gatsby"
import styles from "../styles/toolbar.module.css"
import wikibutton from "../images/wikibutton.png"
import homebrewbutton from "../images/homebrewbutton.png"
import newsletterbutton from "../images/newsletterbutton.png"
import menubutton from "../images/menubutton.png"
import subheader from "./subheader.js"

export const ListLink = props => (
    /*TODO: change this to HoverLink (and somehow pass down all information*/
    <Link to={props.to} hoverto={props.hoverto} hovericonpath={props.hovericonpath} className={styles.icon}>
        <img src={props.iconpath} className={styles.iconimg} alt=""/>
    </Link>
)
export default ({ children }) => (
    <div className={styles.bar}>
        <ListLink to="/" iconpath={wikibutton}/>
        <ListLink to="/homebrew/" iconpath={homebrewbutton}/>
        <ListLink to="/newsletter/" iconpath={newsletterbutton}/>
        <ListLink to="/menu/" iconpath={menubutton}/>
        {children}
    </div>
)

